.rc-taginput {
  padding: 0.375rem 0.75rem;
  font-size: 1rem;
  line-height: 1.5;
  color: black;
  background-color: #fff;
  background-clip: padding-box;
  border: 1px grey solid;
  border-radius: 5px;
  transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
  min-height: 42px;
  display: flex;
  flex-wrap: wrap;
}

.rc-taginput .rc-taginput-badge {
  height: 28px;
  margin: 0 4px;
  padding: 0.25em 0.4em;
  font-size: 75%;
  font-weight: 700;
  line-height: 1;
  border-radius: 0.25rem;
}

.rc-taginput .rc-taginput-badge.rc-taginput-badge-light {
  color: #212529;
  background-color: #f8f9fa;
}

.rc-taginput .rc-taginput-badge button {
  padding: 6px;
  border: none;
  background-color: unset;
  cursor: pointer;
}

.rc-taginput input {
  border: none;
  outline: none;
  width: auto !important;
  flex: 1;
}
