.rc-accordion h5 {
  font-size: 15px;
  color: #818181;
}
.rc-accordion a {
  text-decoration: none;
}
.rc-accordion .collapsed i {
  transform: rotate(0deg);
  font-size: 20px;
  position: absolute;
  right: 13px;
}
.rc-accordion i {
  transform: rotate(180deg);
  font-size: 20px;
  position: absolute;
  right: 13px;
}
.rc-accordion .card-body {
  background: #f3f3f3;
}
.rc-accordion .card-header {
  border: none;
  cursor: pointer;
}
.rc-accordion h4 {
  font-size: 15px;
  font-weight: 700;
}
.rc-accordion .card-body i {
  margin-right: 5px;
  color: #a9a9a9;
  position: inherit;
  font-size: 15px;
  transform: inherit;
}
.rc-accordion .card {
  text-align: left;
}
